<template>
    <div id="rooms">
        <div class="container-fluid">
            <div class="row">
                <div class="col-sm-3 bg-light py-4">
                    <h4 class="fw-bold">飯店資料</h4>
                    <hr>
                    <label for="">總折數</label>
                    <input v-model="this.hotel_discount" type="text" class="form-control mb-2">
                    <label for="">服務費</label>
                    <input v-model="this.service_fee" type="text" class="form-control mb-4">
                    <h4 class="d-inline fw-bold align-bottom">房間編輯 ({{ roomData.length+'間' }})</h4>
                    <button @click="add_rooms" type="button" class="d-block btn btn-secondary mt-2">+新增房間</button>
                    <hr>
                    <div :style="{ height: '45vh', overflowY: 'scroll' }" class="mb-2">
                        <div v-for="(room,index) in roomData" :key="index">
                            <h5 class="fw-bold mb-4 border rounded-1 p-2" :data-bs-toggle="'collapse'" :data-bs-target="'#room_'+index" :style="{ cursor: 'pointer', 'border-left-width': '4px' }">{{ room.name }}
                                <i class="fa-solid fa-trash-can" @click="delete_room(index)"
                                    :style="{ cursor: 'pointer' }"></i>
                            </h5>
                            <div class="collapse" :id="'room_'+index">
                                <label for="">房間名稱
                                </label>
                                <input v-model="room.name" type="text" class="form-control">
                                <label for="">英文名稱</label>
                                <input v-model="room.eng" type="text" class="form-control">
                                <label for="">價錢</label>
                                <input v-model="room.price" type="text" class="form-control">
                                <label for="">折價</label>
                                <input v-model="room.discount" type="text" class="form-control">
                                <label for="">圖片網址</label>
                                <input v-model="room.cover" type="text" class="form-control">
                                <label for="" class="mb-5">房間設備：
                                    <label for="">早餐
                                        <input v-model="room.equipment.breakfast" type="checkbox" class="me-1">
                                    </label>
                                    <label for="">浴缸
                                        <input v-model="room.equipment.bathtub" type="checkbox" class="me-1">
                                    </label>
                                    <label for="">電視
                                        <input v-model="room.equipment.television" type="checkbox" class="me-1">
                                    </label>
                                </label>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-sm-9" :style="{ height: '80vh', overflowY: 'scroll' }">
                    <h1>房間列表</h1>
                    <hr>
                    <div class="">
                        <ul class="row list-unstyled">
                            <li class="col-4 p-4" v-for="(room, index) in roomPrices" :key="index">
                                <div class="shadow-sm">
                                    <div class="position-relative"
                                        :style="{ backgroundImage: `url('${room.cover}')`, backgroundPosition: 'center', backgroundSize: 'cover', height: '150px' }">
                                        <i class="fa-regular fa-circle-xmark fa-2xl position-absolute mt-2 end-0 p-4" style="color: #ffffff; cursor: pointer;" @click="delete_room(index)"></i>
                                        <h3 class="position-absolute top-50 fs-5 px-4 py-1 bg-white fw-bold">{{ room.name }}
                                        </h3>
                                    </div>
                                    <div class="p-4">
                                        <h4 class="d-flex justify-content-between">
                                            {{ room.eng }}
                                            <div class="d-inline-block">
                                                <span v-if="room.equipment.breakfast"><i
                                                        class="fa-solid fa-mug-saucer fa-sm ms-2 opacity-75"></i></span>
                                                <span v-if="room.equipment.bathtub"><i
                                                        class="fa-solid fa-bath fa-sm ms-2 opacity-75"></i></span>
                                                <span v-if="room.equipment.television"><i
                                                        class="fa-solid fa-tv fa-sm ms-2 opacity-75"></i></span>
                                            </div>
                                        </h4>
                                        <h5>{{ room.price }}*{{ room.discount }}*{{ hotel_discount }}={{
                                            room.final_price }}</h5>
                                        <h5>{{ room.discount }}*{{ hotel_discount }} = {{ room.final_discount }}折</h5>
                                        <div class="d-flex ">
                                            <h4>TWD <del>{{ room.price }}</del></h4>
                                            <h4 class="text-danger ms-auto">$ {{ room.final_price }}</h4>
                                        </div>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

// 房間資料
import Swal from 'sweetalert2';
var roomData = [
    {
        "name": "經濟雙人房",
        "eng": "Economy Double Room",
        "price": 7000,
        "amount": 0,
        "cover": "./images/room01.jpg",
        "discount": 0.9,
        "equipment": {
            "television": false,
            "bathtub": true,
            "breakfast": true
        }
    },
    {
        "name": "海景三人房",
        "eng": "Sea view triple Room",
        "price": 7800,
        "amount": 0,
        "cover": "./images/room02.jpg",
        "discount": 0.8,
        "equipment": {
            "television": true,
            "bathtub": true,
            "breakfast": false
        }
    },
    {
        "name": "典雅景觀房",
        "eng": "Elegant landscape Room",
        "price": 5400,
        "amount": 0,
        "cover": "./images/room03.jpg",
        "discount": 0.85,
        "equipment": {
            "television": false,
            "bathtub": true,
            "breakfast": true
        }
    },
    {
        "name": "尊享豪華房",
        "eng": "Exclusive Deluxe Room",
        "price": 9800,
        "amount": 0,
        "cover": "./images/room04.jpg",
        "discount": 0.8,
        "equipment": {
            "television": true,
            "bathtub": false,
            "breakfast": true
        }
    },
    {
        "name": "商務雙人房",
        "eng": "Business Double Room",
        "price": 5600,
        "amount": 0,
        "cover": "./images/room05.jpg",
        "discount": 0.9,
        "equipment": {
            "television": true,
            "bathtub": false,
            "breakfast": false
        }
    },
    {
        "name": "溫泉雙人房",
        "eng": "Hot spring double Room",
        "price": 8400,
        "amount": 0,
        "cover": "./images/room06.jpg",
        "discount": 0.6,
        "equipment": {
            "television": true,
            "bathtub": true,
            "breakfast": true
        }
    },
    {
        "name": "總統套房",
        "eng": "Presidential Suite",
        "price": 23000,
        "amount": 0,
        "cover": "./images/room07.jpg",
        "discount": 0.75,
        "equipment": {
            "television": true,
            "bathtub": true,
            "breakfast": true
        }
    },
    {
        "name": "奢華四人房",
        "eng": "Luxury four Room",
        "price": 8500,
        "amount": 0,
        "cover": "./images/room08.jpg",
        "discount": 0.7,
        "equipment": {
            "television": true,
            "bathtub": true,
            "breakfast": false
        }
    }
];

export default {
    data() {
        return {
            roomData: roomData,
            hotel_discount: 0.9,
            service_fee: 200
        };
    },
    computed: {
        roomPrices() {
            return this.roomData.map(room => {
                return {
                    ...room,
                    final_discount: parseInt(room.discount * this.hotel_discount * 100),
                    final_price: parseInt(room.price * room.discount * this.hotel_discount) + 1.0 * this.service_fee //hotel_discount定義在data()，屬於這個vue元件的屬性，因此需加上this.呼叫
                };
            });
        },
    },
    methods: {
        add_rooms: function () {
            this.roomData.push(
                {
                    "name": "新房間",
                    "eng": "New Room",
                    "price": 0,
                    "amount": 0,
                    "cover": "http://bosscode.monoame.com/20170323_vue_comp/img/room%20(1).jpg",
                    "discount": 0,
                    "equipment": {
                        "wifi": false,
                        "bathtub": true,
                        "breakfast": true
                    }
                },
            );
        },
        delete_room: function (index) {
            Swal.fire({
                title: '確定要刪除房間?',
                text: '這個操作無法撤回',
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#d33',
                cancelButtonColor: '#3085d6',
                confirmButtonText: '確定',
                cancelButtonText: '取消'
            }).then((result) => {
                if (result.isConfirmed) {
                    this.roomData.splice(index,1);
                    Swal.fire('刪除成功', '房間已刪除!', 'success');
                }
            })
        }
    },
    mounted() {
        console.log('ok');
    }
};
</script>

<style>
body {
    padding: 20px;
    overflow-y: hidden;
}
</style>